	module.exports = {"data":{"site":{"siteMetadata":{"title":"Hablando con Máquinas","author":"Jonay Godoy"}},"allMarkdownRemark":{"edges":[{"node":{"excerpt":"Me pareció interesante el concepto de analizar los errores que han llevado al fracaso de un proyecto\ny documentarlo. Puede que al escribirlo...","frontmatter":{"title":"Post Mortem, Amidi","path":"/post-mortem-amidi/","date":"14 July, 2018"}}},{"node":{"excerpt":"Llevo tiempo queriendo escribir un articulo sobre este programa que me ha sido\nbastante útil en algunas ocasiones, generalmente esas...","frontmatter":{"title":"Sweet Home 3D","path":"/sweet-home-crea-habitaciones-en-3d/","date":"01 July, 2018"}}},{"node":{"excerpt":"Primero de todo perdonar por el nombre del articulo no tuve \nimaginación para pensar un nombre mejor. En este capitulo quiero hacer un breve...","frontmatter":{"title":"Libros: JSAllonge, CNV, XP, Apprenticeship","path":"/libros-js-allonge-cnv-xp-apprenticeship/","date":"19 February, 2018"}}},{"node":{"excerpt":"Hace poco que he empezado a tocar c# y con el Visual Studio.\nPor supuesto, esto incluye el plugin ReSharper de jetbrains para Visual Studio...","frontmatter":{"title":"ReSharper, Search Pattern","path":"/resharper-search-pattern/","date":"14 January, 2018"}}},{"node":{"excerpt":"Hace muy poco me enseñaron la arquitectura modelo-vista-presenter con vista pasiva en javascript \ny no pude resistirme a hacer una prueba de...","frontmatter":{"title":"Vista Pasiva","path":"/vista-pasiva/","date":"17 December, 2017"}}},{"node":{"excerpt":"Hoy voy a hablar del comando de git “—no-ff” sobretodo porque cuando vi este comando\nme pregunte ¿Por que alguien iba a querer evitar el...","frontmatter":{"title":"No Fast Forward","path":"/no-fast-forward/","date":"03 December, 2017"}}},{"node":{"excerpt":"Este es el primer año que se hace el HackForGood BigDay por lo que creo que\nes una buena idea contar mi experiencia para que otras personas...","frontmatter":{"title":"Hack For Good Big Day","path":"/hack-for-good-big-day/","date":"29 September, 2017"}}},{"node":{"excerpt":"Buenas, yo se nada sobre la mejor o la peor o manera de hacer un curriculum.Pero si sé una cosa odio invertir tiempo en mantener información...","frontmatter":{"title":"Convierte tu perfil Linkedin en un curriculum","path":"/convierte-tu-perfil-de-Linkedin-en-tu-cv/","date":"12 September, 2017"}}},{"node":{"excerpt":"Screeps Se describe como un juego para programadores. Programa a tu colonia de creeps para que se expanda y conquiste nuevas zonas. Un MMO...","frontmatter":{"title":"Empezamos con los juegos educativos.Screeps y Codecombat","path":"/empezamos-con-los-juegos-educativos/","date":"09 September, 2017"}}},{"node":{"excerpt":"Los métodos estáticos en general, hace nuestro día a día un poco más cómodo pero si tienes una mínima duda de si deberías utilizarlo lo...","frontmatter":{"title":"Como testear métodos estáticos","path":"/como-testear-metodos-estaticos/","date":"01 July, 2017"}}},{"node":{"excerpt":"Me parecía adecuado escribir como uno de los primeros artículos\nblog. Todo el trayecto que tiene mi blog porque aunque me dé algo\nde...","frontmatter":{"title":"La Historia De Mi Blog","path":"/la-historia-de-mi-blog/","date":"30 May, 2017"}}},{"node":{"excerpt":"Últimamente me encontrado con muchos programadores que no ven el switch como un mal olor.\nEs más hace alrededor de un año yo tampoco lo...","frontmatter":{"title":"Kata Gilded Rose Switch","path":"/kata-gilded-rose-switch/","date":"30 May, 2017"}}},{"node":{"excerpt":"Buenas, hoy quería hablar sobre mi primera experiencia ludum. Aunque antes os voy a poner en contexto para todo aquel que desconozca el...","frontmatter":{"title":"Ludum Dare GC 35, Mi primera Ludum","path":"/ludum-dare-gc-35-mi-primera-ludum/","date":"29 December, 2016"}}}]},"markdownRemark":{"id":"D:/blogRepository/hablando-con-maquinas/src/pages/articles/2017-09-29-hack-for-good-Big-day/index.md absPath of file >>> MarkdownRemark","html":"<p>Este es el primer año que se hace el HackForGood BigDay por lo que creo que\nes una buena idea contar mi experiencia para que otras personas puedan conocerlo\ndesde mi perspectiva y también en cierta forma para mi futuro yo.</p>\n<p>Yo participe en hack for good y quede segundo con el proyecto Amidi.\nTuve suerte, a pesar del que el proyecto no quedo primero pudimos\nentrar en la repesca y conseguir una plaza para la final.</p>\n<p>Aunque no sea lo más importante la verdad, es que tuvimos los gastos del viaje, alojamiento\ny transporte pagados.No sé para ustedes pero para mi esto es un lujo.</p>\n<p>En el aeropuerto, me encontré con otros participantes de nuestra sede,\naprovechamos y nos pusimos al día de nuestros respectivos proyectos.\nAsí hasta que llegamos a Madrid y nos encontramos con Sergio el encargado de nuestra\nsede.</p>\n<p>Dejamos las cosas en el hotel, fuimos a cenar y nos pasamos parte de la noche\nponiéndonos al día y enterándonos del evento de mañana.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/night-1c82204195de5426c9d495f3ff1008f2-3f5a1.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"llegada\"\n        title=\"\"\n        src=\"/static/night-1c82204195de5426c9d495f3ff1008f2-f8fb9.jpg\"\n        srcset=\"/static/night-1c82204195de5426c9d495f3ff1008f2-e8976.jpg 148w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-63df2.jpg 295w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-f8fb9.jpg 590w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-85e3d.jpg 885w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-d1924.jpg 1180w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-9452e.jpg 1770w,\n/static/night-1c82204195de5426c9d495f3ff1008f2-3f5a1.jpg 2048w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>Preparándome la charla</h2>\n<p>Después de dar una vuelta por las calles de Madrid con los otros participantes de nuestra sede\nme puse al terminar el pitch, llevaba más de la mitad escrita, pero quería esperar\na hablar con mi compañero para terminarla porque él había tenido un contacto con escuelas\ndonde llevo nuestro proyecto.</p>\n<p>Se comentó el tema de presentarlo mi compañero y yo juntos, pero yo me negué según mi experiencia\nlos pitchs suele funcionar mejor si solo expone 1 y después de hablarlo nos pusimos de\nacuerdo.</p>\n<p>Para terminar el día me puse a ensayarlo. A veces me funciona bien lo de memorizar cosas\nantes de ir a dormir y repasarlas al despertar porque cuando duermes los recuerdos\npasa a guardarse a la memoria a largo plazo de esta manera no solo lo tengo en la memoria\na corto plazo y me es más fácil recordar, en teoría.\nPor lo que estuve ensayando hasta que al menos recordara todo el pitch\nde memoria sin mirar el guion. Luego me fui a dormir.</p>\n<p>Por la mañana salí sin despertar a mi compañero y me puse a ensayar donde pude.\nQuizás otras personas no lo hubieran dedicado horas de sueño a esto.\nyo lo vi como una de esas pequeñas oportunidades de dedicar buen número de horas\nen mejorar una habilidad realmente importante y que normalmente no le dedico tiempo\nporque hay otras prioridades.</p>\n<p>Por otro lado, hace un tiempo, en un hackathon vi una presentación que me encantó y su secreto\nfue ensayarla durante al menos 4 horas. No con estás palabras exactas pero me dio a entender que\ncuando presentas un proyecto cargas con el trabajo del equipo y muchas veces de ti termina\ndependiendo que ese trabajo de resultados.</p>\n<h2>Empezando el Big Day</h2>\n<p>Cuando nos separamos la noche anterior no definimos a ciencia cierta la hora de diana\nno fue muy grave lo único que tuvimos que movernos y desayunar con algunas\nprisas.</p>\n<p>Llegamos un poco tarde, pero en esta ocasión era uno de esos eventos que arranca entre\n10 y 15 min más tarde de la hora. Normalmente, esto me disgusta pero\nen este contexto tras salir con prisas y con el cansancio del viaje realmente\nagradecí poder sentarme tranquilamente 10 min a esperar que empezará el evento\ny creo que otros participantes se sentían como yo, aunque quizás es mucho suponer.\n</p>\n<h2>Las Charlas</h2>\n<p>Cuando empezaron las presentaciones yo me peleaba por controlar mi corazón acelerado y\npor eso me afecto especialmente una de las charlas que abría el evento\nde\n<a href=\"https://twitter.com/FabianGPastor\" target=\"_blank\">Fabian Garcia Pastor</a>,\ny se podría resumir en “no tengáis miedo disfrutar el momento”.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/fabian-fb799ee5c2447e90691963d3fad2f26b-30631.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"fabianGPastor\"\n        title=\"\"\n        src=\"/static/fabian-fb799ee5c2447e90691963d3fad2f26b-f8fb9.jpg\"\n        srcset=\"/static/fabian-fb799ee5c2447e90691963d3fad2f26b-e8976.jpg 148w,\n/static/fabian-fb799ee5c2447e90691963d3fad2f26b-63df2.jpg 295w,\n/static/fabian-fb799ee5c2447e90691963d3fad2f26b-f8fb9.jpg 590w,\n/static/fabian-fb799ee5c2447e90691963d3fad2f26b-85e3d.jpg 885w,\n/static/fabian-fb799ee5c2447e90691963d3fad2f26b-d1924.jpg 1180w,\n/static/fabian-fb799ee5c2447e90691963d3fad2f26b-30631.jpg 1600w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Me ayudo, me hizo recordar que muchas veces pensar en lo que no debes hacer no ayuda.\nPuede que este exagerando, pero es considerable como la diferencia del\npensar en “disfrutar” en vez de pensar “no estar nervioso” me influye.\nCreo que la diferencia reside en que con una nos limitamos y con\nla otras nos centramos, pero por supuesto esto es mi opinión personal.</p>\n<p>Fueron avanzando los grupos y escuchando pequeñas charlas hasta el descanso, incluyendo\nesta muy divertida de\n<a href=\"https://www.youtube.com/watch?v=3w95FZ1g1hc\" target=\"_blank\">como hacer un pitch</a>\nsobretodo porque más tarde tuve el privilegio de compartir ascensor\ncon D. César Alierta y Dña. Trinidad Jiménez,\ntambién hablaron del\n<a href=\"https://www.youtube.com/watch?v=icXyBJT7wow\" target=\"_blank\">cambio de paradigma</a>,\nla presentación del director\nde Wayra o los proyectos tecnológicos en la escuela de tenis de Rafa Nadal.</p>\n<p>Otra charla que me llamo atención fue una sobre <strong>mapas de talento</strong>. La historia de esta\ncharla comienza unos días antes del Big Day. Me llego un mensaje a última hora.\nEn parte se puede llegar entender que no fuera fácil contactar con todos miembros de los 17 equipos\npero tenía un margen de 2 horas para resolverlo estuve a punto\nde negarme a hacer el “test”. Sin embargo ahora me alegro de haber participado.\nPongo “test” entre comillas porque creo que realmente se trata de un mapa de talento digital y\nno solo tiene el formato clásico de un test. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/mapa-talento-443b61c02838aad86330d73091f2cf90-b2a60.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"mapa-talento\"\n        title=\"\"\n        src=\"/static/mapa-talento-443b61c02838aad86330d73091f2cf90-f8fb9.jpg\"\n        srcset=\"/static/mapa-talento-443b61c02838aad86330d73091f2cf90-e8976.jpg 148w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-63df2.jpg 295w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-f8fb9.jpg 590w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-85e3d.jpg 885w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-d1924.jpg 1180w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-9452e.jpg 1770w,\n/static/mapa-talento-443b61c02838aad86330d73091f2cf90-b2a60.jpg 3264w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Pero todo sea dicho el “test” me encantó. Generalmente, los test son para mi papel mojado,\nporque si bien sabes que los test pueden ser útiles sí\nse hacen bien la mayoría de los que me llegan son demasiados genéricos y como mucho solo\ndicen medias verdades. Este en cambio, me pareció desde mi ignorancia en esta clase de temas bien\nhecho y además en mi caso acertó con cierto nivel de detalle.</p>\n<p>Dejo\n<a href=\"https://drive.google.com/open?id=0B2JGlpvHuADfdXZBdmlRNUotM1k\" target=\"_blank\">los resultado de mi test</a>\naunque me da un poco de vergüenza publicar mis resultados\nespero que sirva para que podáis valorar el mapa mental. </p>\n<p>En la charla se comentó los resultados generales y haciendo énfasis\nlos perfiles que habían encontrado.\nla verdad es que fue interesante sobretodo para los que lo hicieron.</p>\n<p>Por último, se presentó el curso de 9 nuevas habilidades digitales que pronto estará disponible en la plataforma\n<a href=\"https://miriadax.net\" target=\"_blank\">miradaX</a></p>\n<p>Que como bien decían en la presentación son habilidades que nunca se terminan de aprender del todo\ny le echaré un vistazo parece interesante.</p>\n<p>Hubo más charlas y no nos olvidemos de las presentaciones, todo ello interesante pero aquí recojo lo que más llamo\nla atención.</p>\n<h2>Mi Presentación</h2>\n<p>Justo después del descanso me tocaba presentar a mí. No estaba demasiado nervioso\nla pausa me ayudo fui al baño y después vi algún proyecto de otros equipos.</p>\n<p>Se acabó el descanso, me llamaron subí al escenario, me vendí y termine.\nRealmente una charla es simplemente eso.</p>\n<p>Las personas con las que hable antes y después\ndel evento me dieron muy buen feedback. Creo que hasta hoy es la mejor presentación\nque hecho en mi vida y aunque eso puede que no sea mucho para mi es punto de referencia.</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 75%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <html><head></head><body><iframe src=\"https://drive.google.com/file/d/0B2JGlpvHuADfVUZqMDcxaDJTeDg/preview\" style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe></body></html>\n          </div>\n          </div>\n<p>He decidido subir mi presentación en video sobretodo para recordarla dentro de unos años.\nPor supuesto el feedback es bienvenido. Desde mi punto de vista algunos de mis errores\nde los cuales ninguno fue grave, uno fue que estaba acelerado. Por lo que muchas veces perdía\nun poco el control de lo que pasaba a mi alrededor, del guion y de mi voz.\nAunque haberlo memorizado ayudo a compensarlo.\nEl segundo creo que es algo le suele ocurrir a todo el mundo.\nEl problema es, que como yo me escucho, es distinto a como me oyen los demás eso afecta\na mi tono y timbre además vocalizar un poco no me\nvendría mal. De todas maneras como dije el feedback que me dieron fue muy positivo y\nestoy satisfecho con el resultado.</p>\n<h2>Proyectos ganadores</h2>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/entrega-1213deac4e0561ccf360a216afd90bb9-30631.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Entrega-de-premios\"\n        title=\"\"\n        src=\"/static/entrega-1213deac4e0561ccf360a216afd90bb9-f8fb9.jpg\"\n        srcset=\"/static/entrega-1213deac4e0561ccf360a216afd90bb9-e8976.jpg 148w,\n/static/entrega-1213deac4e0561ccf360a216afd90bb9-63df2.jpg 295w,\n/static/entrega-1213deac4e0561ccf360a216afd90bb9-f8fb9.jpg 590w,\n/static/entrega-1213deac4e0561ccf360a216afd90bb9-85e3d.jpg 885w,\n/static/entrega-1213deac4e0561ccf360a216afd90bb9-d1924.jpg 1180w,\n/static/entrega-1213deac4e0561ccf360a216afd90bb9-30631.jpg 1600w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><strong>1º</strong> premio. <strong>Same Bullying</strong> para prevenir el acoso en redes sociales\n<a href=\"https://twitter.com/SAMEBullying\" target=\"_blank\">+info</a></p>\n<p><strong>2º</strong> premio. <strong>Luz Verde</strong> Su propuesta técnica es capaz de “traducir” el estado de los semáforos para ayudar\na las personas invidentes\n<a href=\"https://www.youtube.com/watch?v=sAzv6crvTx8&feature=youtu.be\" target=\"_blank\">+info</a></p>\n<p><strong>3º</strong> premio. <strong>Easy Moves</strong> adapta la silla de ruedas eléctrica convencional para que pueda ser controlada a través de una aplicación por reconocimiento de voz,\ntracking de la cabeza y estímulos cerebrales.</p>\n<p>Ninguno de las palmas resultó ganador.Había proyectos muy buenos según creo\nen varios casos solo tuvieron un punto o dos de diferencia,\nllegando a ocurrir que el 2º y el 3º tercer puesto empataron. Por lo que no hay nada de que\nlamentarse.</p>\n<p>Los proyectos de nuestra sede fueron PicComunica\n<a href=\"https://twitter.com/PicComunica\" target=\"_blank\">+info</a>\nque después del big day han obtenido un segundo premio en Venture4Grancanaria ,\nel mio Amidi\n<a href=\"http://www.aprendeconamidi.com/\" target=\"_blank\">+info</a>,\ny Keekuo\n<a href=\"https://twitter.com/Keekuo_\" target=\"_blank\">+info</a></p>\n<h2>Almuerzo y Visita a  <a href=\"https://twitter.com/WayraES\" target=\"_blank\">WayraES</a></h2>\n<p>Después del evento almorzamos en la sala del consejo de administración de telefónica\nque aunque no fue muy interesante, sí que fue muy divertido\nel lugar era impresionante en muchos sentidos con vistas de todo Madrid.\nSentarte allí, molaba mucho te daban ganas de soltarte un risa malvada buajajajaja.\nNo solo me afectaba a mí se notaba como\nal entrar cambiaba el estado de ánimo de lo equipos como dije esto no fue\npara nada importante pero si fue bastante divertido. La foto no le hace justicia a la sala.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-6c290.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 39.760265238459574%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"lasala\"\n        title=\"\"\n        src=\"/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-f8fb9.jpg\"\n        srcset=\"/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-e8976.jpg 148w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-63df2.jpg 295w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-f8fb9.jpg 590w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-85e3d.jpg 885w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-d1924.jpg 1180w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-9452e.jpg 1770w,\n/static/lasala-68cbee55e19b6ce1777c00f3ea1cc1d7-6c290.jpg 7842w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Por último fuimos a Wayra, escuchamos dos proyectos. Los\nproyectos eran interesantes, pero me centraré en Wayra. Wayra junto a open future\nes una de las principales incubadoras de telefónica. Para estar en Wayra se debe haber\npasado por open future.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-3f5a1.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 34.1796875%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"wayra\"\n        title=\"\"\n        src=\"/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-f8fb9.jpg\"\n        srcset=\"/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-e8976.jpg 148w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-63df2.jpg 295w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-f8fb9.jpg 590w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-85e3d.jpg 885w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-d1924.jpg 1180w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-9452e.jpg 1770w,\n/static/wayra-2945dcac5a93d1f4345a82db28ce6d73-3f5a1.jpg 2048w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Algunas cosas que más me llamaron la atención sobre Wayra:</p>\n<ul>\n<li>El lugar físicamente es como un coworking con estilo propio está bastante bien.</li>\n<li>Solo 1 o 2 % de los proyectos que se presentaba para el open future llega a Wayra.\nLa estadística está justifica por la enorme cantidad de proyectos que intenta entrar en\nopen future.</li>\n<li>Estando dentro de Wayra, si quieres aprender casi cualquier cosa puedes llamar\ny te traen a un mentor para que venga en persona a enseñarte.</li>\n<li>Wayra financia tu proyecto con alrededor de 100.000 € si no recuerdo mal, más licencias\npara todo tipo de programas.</li>\n</ul>\n<h2>Impresiones Personales</h2>\n<p>Cuando participe en el hack for good en las palmas, a pesar de estar rodeado de personas sobresalientes\nno fui capaz de conocer a nadie fuera de mi grupo, debido al caos que hay\nen cualquier hackathon. Para ser sincero casi no me acordaba de sus caras.\nMe alegro de haber tenido esta segunda oportunidad para acercarme a ellos y a\n<a href=\"https://twitter.com/sergiotrasmallo\" target=\"_blank\">Sergio Sánchez</a>\nel encargado de nuestra sede, solo por eso valió la pena el viaje.</p>\n<p> \n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-a8a50.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"participante las palmas\"\n        title=\"\"\n        src=\"/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-f8fb9.jpg\"\n        srcset=\"/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-e8976.jpg 148w,\n/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-63df2.jpg 295w,\n/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-f8fb9.jpg 590w,\n/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-85e3d.jpg 885w,\n/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-d1924.jpg 1180w,\n/static/las-palmas-4f1f0fecc63f2c3db9e77fd5f0665bab-a8a50.jpg 1280w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Por otro lado, me llevo una sensación un poco amarga.\nSin ser un sentimiento malo del todo, me sentí abrumado.\nEstar rodeado de tantos profesionales que serán mejores o peores que yo en distintas\ncosas pero que en general saben trabajar tanto de otros grupos, como los que nos presentó\ntelefónica me hace sentir como pez en medio del océano. Entre tantos profesionales competentes\n¿Cómo se marca la diferencia? </p>\n<p>Todo esto fue solo un gran día de mi vida. Fue muy enriquecedor e intenso.\nSiempre me sorprende lo que se puede hacer con solo 24 horas bien organizadas,\nPor último, gracias a la\n<a href=\"https://twitter.com/ULPGC\" target=\"_blank\">ULPGC</a>\n, a telefónica y todas las personas que trabajaron para\nque yo pudiera vivir este día.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-fef88.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.16635397123201%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Gracias\"\n        title=\"\"\n        src=\"/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-f8fb9.jpg\"\n        srcset=\"/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-e8976.jpg 148w,\n/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-63df2.jpg 295w,\n/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-f8fb9.jpg 590w,\n/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-85e3d.jpg 885w,\n/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-d1924.jpg 1180w,\n/static/gracias-d96eaea90a5c6eb75490741770ea4eb6-fef88.jpg 1599w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"title":"Hack For Good Big Day","path":"/hack-for-good-big-day/","date":"September 29, 2017","image_article":{"relativePath":"articles/2017-09-29-hack-for-good-Big-day/coverMain.jpg"}}}},"pathContext":{"path":"/hack-for-good-big-day/"}}

